import { Component, h, Element, Prop } from '@stencil/core';

@Component({
  tag: 'ks-order-summary-container',
})
export class OrderSummaryContainer {

    @Element() root: HTMLElement;

    componentDidLoad(){
        this.root.style.display = "block"
    }

    @Prop() heading: string = "";
    @Prop() returnHeading: string = "";
    @Prop() returnLink: string = "";

    render() {
        return[
            <div class="uk-padding-small uk-padding@s uk-margin-bottom">
                <div class="uk-text-center uk-text-bold ks-text-decorated uk-margin-medium-bottom uk-margin-top" style={{fontSize: "26px"}}>
                    {this.heading}
                </div>
        
                <div style={{maxWidth: "700px", margin: "auto"}} uk-margin>

                    <slot />
                </div>
            </div>,
            <a href={this.returnLink} class="uk-button uk-button-secondary uk-width-1-1 uk-width-expand@m ks-text-decorated"
                    style={{fontSize: "24px", fontWeight: "700", padding: "20px"}}>
                {this.returnHeading}
            </a>
        ];
    }
}